@import url('https://fonts.googleapis.com/css?family=family=Archivo+Black|Judson:400,700');

.layer0 {
  background-image: url('../../assets/layer0.svg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  z-index: 0;
}

.layer1 {
  position: absolute;
  background-image: url('../../assets/layer1.svg');
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 7em;
  height: 88.5vh;
  width: 100vw;
  z-index: 2;
}

.name {
  display: flex;
  color: white;
  width: 100%;
  opacity: 0;
  text-align: left;
  padding-top: 0.5em;
  padding-left: 0.5em;
  font-size: 2em;
  font-family: 'Archivo Black', sans-serif;
  letter-spacing: 0.2em;
  font-weight: 600;
  height: auto;

  animation: fadeIn 2s ease-out forwards;
  animation-delay: 1s;
}

.textLoop {
  animation: fadeIn 2s ease-out forwards;
  animation-delay: 2s;
  padding-left: 0.5em;
}

.sub-title {
  color: white;
}

.left-clouds {
  position: fixed;
  background-image: url('../../assets/left_clouds.svg');
  background-repeat: no-repeat;
  background-size: cover;
  left: 0;
  height: 16em;
  width: 30em;
  margin-top: 9.6em;
  z-index: 1;

  animation: moveInRight 1.5s ease-out;
}

.right-clouds {
  position: fixed;
  background-image: url('../../assets/right_clouds.svg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 24em;
  width: 70em;
  right: 0;
  margin-top: 5.6em;
  z-index: 1;

  animation: moveInLeft 1.5s ease-out;
}

svg {
  position: absolute;
}

@keyframes moveInRight {
  /* Initial state */
  0% {
      opacity: 0;
      transform: translateX(-20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      transform: translate(0);
  }
}

@keyframes moveInLeft {
  /* Initial state */
  0% {
      opacity: 0;
      transform: translateX(20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      transform: translate(0);
  }
}

@keyframes fadeIn {
  /* Initial state */
  0% {
      opacity: 0;
  }

  /* Final state */
  100% {
      opacity: 1;
  }
}
