.socialIcon {
  display: flex;
  white-space:nowrap;
  cursor: pointer;
  text-decoration: none;
}

.platform-title {
  align-self: center;
  margin-left: 0.5em;
  opacity: 0;
  max-width: 0;
  text-decoration: none;
  transition: max-width 1s ease-out .1s, opacity 1s ease-out .1s, color;
}

.socialIcon:hover .platform-title{
  opacity: 1;
  transition: max-width 1s ease-out .1s, opacity 1s ease-out .1s, color .2s;
}