@import url(https://fonts.googleapis.com/css?family=family=Archivo+Black|Judson:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.layer0 {
  background-image: url(/static/media/layer0.45a54b54.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  z-index: 0;
}

.layer1 {
  position: absolute;
  background-image: url(/static/media/layer1.23b95370.svg);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 7em;
  height: 88.5vh;
  width: 100vw;
  z-index: 2;
}

.name {
  display: flex;
  color: white;
  width: 100%;
  opacity: 0;
  text-align: left;
  padding-top: 0.5em;
  padding-left: 0.5em;
  font-size: 2em;
  font-family: 'Archivo Black', sans-serif;
  letter-spacing: 0.2em;
  font-weight: 600;
  height: auto;

  -webkit-animation: fadeIn 2s ease-out forwards;

          animation: fadeIn 2s ease-out forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.textLoop {
  -webkit-animation: fadeIn 2s ease-out forwards;
          animation: fadeIn 2s ease-out forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  padding-left: 0.5em;
}

.sub-title {
  color: white;
}

.left-clouds {
  position: fixed;
  background-image: url(/static/media/left_clouds.02a2333d.svg);
  background-repeat: no-repeat;
  background-size: cover;
  left: 0;
  height: 16em;
  width: 30em;
  margin-top: 9.6em;
  z-index: 1;

  -webkit-animation: moveInRight 1.5s ease-out;

          animation: moveInRight 1.5s ease-out;
}

.right-clouds {
  position: fixed;
  background-image: url(/static/media/right_clouds.a5721831.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 24em;
  width: 70em;
  right: 0;
  margin-top: 5.6em;
  z-index: 1;

  -webkit-animation: moveInLeft 1.5s ease-out;

          animation: moveInLeft 1.5s ease-out;
}

svg {
  position: absolute;
}

@-webkit-keyframes moveInRight {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(-20em);
              transform: translateX(-20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@keyframes moveInRight {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(-20em);
              transform: translateX(-20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@-webkit-keyframes moveInLeft {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(20em);
              transform: translateX(20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@keyframes moveInLeft {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(20em);
              transform: translateX(20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@-webkit-keyframes fadeIn {
  /* Initial state */
  0% {
      opacity: 0;
  }

  /* Final state */
  100% {
      opacity: 1;
  }
}

@keyframes fadeIn {
  /* Initial state */
  0% {
      opacity: 0;
  }

  /* Final state */
  100% {
      opacity: 1;
  }
}

.body {
  background-color: #322e49;
  display: flex;
  justify-content: space-between;
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 2;
}

.textArea {
  width: 50vw;
  padding: 1em;
  margin-left: 3em;
}

.textArea h1 {
  color: #dc2c2c;
  font-family: 'Helvetica Neue', sans-serif; font-size: 275px;
  font-weight: bold;
  letter-spacing: -1px;
  line-height: 1;
  margin-bottom: 0em;
  margin-top: 0.5em;
  text-align: center;
}

.textArea h2 {
  color: #b4b4b4;
  font-family: 'Open Sans', sans-serif;
  font-size: 2em;;
  font-weight: 300;
  line-height: 2em;;
  text-align: center;
}

.textArea p {
  color: #c0bdbd;
  font-family: 'Helvetica Neue', sans-serif;
  font-size: 1.1em;
  line-height: 24px;
  margin: 0 0 24px;
  text-align: justify;
  text-justify: inter-word;
}

.link {
  color: #1570A6;
  text-decoration: none;
}

.link:hover {
  color: #0a97e9;
}

.socialPanel-container {
  margin-top: 5em;
  margin-right: 2em;
  margin-bottom: 5em;
}

.socialPanel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  height: 100%;
  width: 100%;
}
.socialIcon {
  display: flex;
  white-space:nowrap;
  cursor: pointer;
  text-decoration: none;
}

.platform-title {
  align-self: center;
  margin-left: 0.5em;
  opacity: 0;
  max-width: 0;
  text-decoration: none;
  -webkit-transition: max-width 1s ease-out .1s, opacity 1s ease-out .1s, color;
  transition: max-width 1s ease-out .1s, opacity 1s ease-out .1s, color;
}

.socialIcon:hover .platform-title{
  opacity: 1;
  -webkit-transition: max-width 1s ease-out .1s, opacity 1s ease-out .1s, color .2s;
  transition: max-width 1s ease-out .1s, opacity 1s ease-out .1s, color .2s;
}
body {
  height: 100%;
  width: 100%;
}

.App {
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

